// import Engine from './utils/engine';
import Engine from './utils/engine3d';

import TileBox from './TileBox';

const WIDTH = 480;
const HEIGHT = 480;

const el = document.getElementById('container');

const app = new Engine(el, {
  debug: false,
  width: WIDTH,
  height: HEIGHT
}, {
  alpha: true
});

app.setCamera(Engine.PERSPECTIVE_CAMERA(WIDTH, HEIGHT), true, {
  enableZoom: true
});


const letters = [
  ['H', 'M'],
  ['E', 'E'],
  ['A', 'T'],
  ['V', 'A'],
  ['Y', 'L'],
]

let changed = 0;
let side = 0;
function onChange() {
  changed++;

  if (changed === 1) {
    side = (side + 1) % 2;

    document.body.style.backgroundColor = boxes[0].colours[(side + 1) % 2];
  }

  if (changed >= letters.length) {
    changed = 0;
  }
}

const boxes = new Array(letters.length).fill(0).map((c, idx) => {
  const box = new TileBox(app.scene, letters[idx], {
    onChange: onChange
  });
  box.shapes.position.x = (idx * 60) - (((letters.length - 1) * 60) / 2);

  const delay = idx * 50;
  box.play(
    delay,
    (letters.length * 50) - delay
  );
  return box;
});

app.onRender(({ frame, width, height, debug }) => {
  boxes.forEach(box => box.update());
});

app.start();
