import Loop from 'raf-loop';
import { Scene, OrthographicCamera, WebGLRenderer } from 'three';
// import { OrbitControls } from '../../examples/jsm/controls/OrbitControls.js';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';

const DEFAULTS = {
  debug: false,
  width: 480,
  height: 480,
  pixelRatio: window.devicePixelRatio,
  clickToggleDebug: true,
};

const RENDERER_DEFAULTS = {
  alpha: false
};

class SketchEngine3D {
  constructor(mountEl, opts = {}, rendererOpts = {}) {
    this.options = {
      ...DEFAULTS,
      ...opts
    };
    this.rendererOptions = {
      ...RENDERER_DEFAULTS,
      ...rendererOpts
    };

    this.mountEl = mountEl;

    this.scene = new Scene();

    this.renderer = new WebGLRenderer(this.rendererOptions);

    this.updateDimensions(this.options.width, this.options.height, this.options.pixelRatio);
    this.mountEl.appendChild(this.renderer.domElement);

    this.frame = 0;

    this.loop = Loop(this.update.bind(this));

    this._renderer = () => {};
  }

  setCamera(camera, orbitControls = false, { autoRotate = false, enableZoom = false} = {}) {
    this.camera = camera;

    if (orbitControls) {
      this.orbit = new OrbitControls(this.camera, this.renderer.domElement);
      this.orbit.autoRotate = autoRotate;
      this.orbit.enableZoom = enableZoom;
    } else {
      this.orbit = null;
    }
  }

  _addEventListeners() {
    this.mountEl.addEventListener('click', () => {
      this.options.debug = !this.options.debug;

      document.body.classList.toggle('debug', this.options.debug);
    }, false);
  }

  updateDimensions(width, height, pixelRatio) {
    this.mountEl.style.width = `${width}px`;
    this.mountEl.style.height = `${height}px`;

    this.renderer.setSize(width, height);
    this.renderer.setPixelRatio(pixelRatio);
  }

  onRender(fn = () => {}) {
    this._renderer = fn;
  }

  start() {
    this.loop.start();
  }

  update(dt) {
    if (!this.camera) {
      console.log("There is no camera");

      return false;
    }

    this._renderer({
      debug: this.options.debug,
      frame: this.frame,
      deltaTime: dt,
      width: this.options.width,
      height: this.options.height
    });

    if (this.orbit) {
      this.orbit.update();
    }

    this.renderer.render(this.scene, this.camera);
    this.frame++;
  }

  stop() {
    this.loop.stop();
  }

  static PERSPECTIVE_CAMERA(width, height) {
    const camera = new OrthographicCamera(width / -2, width / 2, height / 2, height / -2, 1, 1000);
    camera.position.z = 500;
    return camera;
  }
}

export default SketchEngine3D;
