import { PlaneGeometry, MeshBasicMaterial, Mesh, DoubleSide, Object3D, Matrix4 } from 'three';

class Tile {
  constructor(width, height, opts = {}) {
    const DEFAULTS = {
      canvasSize: 256
    };

    this.options = {
      ...DEFAULTS,
      ...opts
    };

    this.width = width;
    this.height = height;

    return this;
  }

  createShape(textures) {
    const shape = new Object3D();

    textures.forEach((t, idx) => {
      const geo = new PlaneGeometry(this.width, this.height);

      if (idx % 2 === 1) {
        geo.applyMatrix( new Matrix4().makeRotationY( Math.PI ) );
      }

      const mat = new MeshBasicMaterial({
        map: t
      });
      const plane = new Mesh(geo, mat);

      shape.add(plane);
    });

    return shape;
  }
}

export default Tile;
