import { CanvasTexture, Group, Math as ThreeMath } from 'three';
import anime from 'animejs/lib/anime.es.js';

import Tile from './Tile';
import {getCanvas, drawTextCenter} from './canvas';

class TileBox {
  constructor(scene, txt, opts = {}) {
    const DEFAULTS = {
      canvasSize: 256,
      tiles: 10,
      tileGutter: 5,
      size: 50,
      onChange: () => {}
    };

    this.options = {
      ...DEFAULTS,
      ...opts
    };

    this.scene = scene;

    this.canvases = new Array(2).fill(0).map(() => {
      const { canvas, ctx } = getCanvas(this.options.canvasSize);

      // document.body.appendChild(canvas);

      return {
        canvas,
        ctx
      };
    });

    this.textures = new Array(2).fill(0).map((c, idx) => {
      return new CanvasTexture(this.canvases[idx].canvas);
    });

    this.shapes = new Group();

    for (let i = 0; i < this.options.tiles; i++) {
      const tile = new Tile(this.options.size, this.options.size);
      const tileShape = tile.createShape(this.textures);
      tileShape.position.z = (i * this.options.tileGutter) - ((this.options.tiles * this.options.tileGutter) / 2);
      this.shapes.add(tileShape);
    }

    this.scene.add(this.shapes);
    this.rotator = {
      x: 0,
      y: 0,
      z: 0,
      stage: 0
    };

    this.currStage = 0;

    this.colours =  ['red', 'blue'];

    this.setText(txt);
  }

  setText(txt) {
    this.canvases.forEach(({ canvas, ctx }, idx) => {
      ctx.clearRect(0, 0, this.options.canvasSize, this.options.canvasSize);
      ctx.fillStyle = this.colours[idx];
      ctx.fillRect(0, 0, this.options.canvasSize, this.options.canvasSize);
      ctx.fillStyle = 'white';
      drawTextCenter(txt[idx], canvas, {
        fontSize: this.options.canvasSize,
        rotate: idx % 2 === 0 ? 0 : Math.PI * 0.5
      });
      this.textures[idx].needsUpdate = true;
    });
  }

  play(delay = 0, endDelay = 0) {

    anime({
      targets: this.rotator,
      // x: ThreeMath.degToRad(360),
      keyframes: [
        {
          y: ThreeMath.degToRad(-180),
          z: ThreeMath.degToRad(-90),
          // x: ThreeMath.degToRad(-180),
          stage: 1
        },
        {
          y: ThreeMath.degToRad(-360),
          z: ThreeMath.degToRad(0),
          // x: ThreeMath.degToRad(-360),
          stage: 0
        }
      ],
      duration: 1500,
      delay,
      endDelay,
      easing: 'easeInOutCubic',
      loop: true
    });
  }

  update() {
    // console.log(this.rotator);
    this.shapes.rotation.x = this.rotator.x;
    this.shapes.rotation.y = this.rotator.y;
    this.shapes.rotation.z = this.rotator.z;

    const stage = Math.round(this.rotator.stage);

    if (this.currStage !== stage) {
      this.currStage = stage;
      this.options.onChange();
    }
  }
}

export default TileBox;
