const isMultipleOfEight = num => {
  return (num / 8) === Math.round(num / 8);
};

export const getCanvas = (size) => {
  if (!isMultipleOfEight(size)) {
    console.error('Size is not multiple of 8');
    return false;
  }

  const canvas = document.createElement('canvas');

  canvas.width = canvas.height = size;

  return {
    canvas,
    ctx: canvas.getContext('2d')
  };
}

export const drawTextCenter = (text, canvas, { weight = 'bold', fontSize = 32, rotate = 0 } = {}) => {
  const ctx = canvas.getContext('2d');
  ctx.font = `${weight} ${fontSize}px Helvetica`;

  const { width } = ctx.measureText(text);

  const xOffset = -(width / 2);
  const yOffset = (fontSize * 0.375);

  ctx.save();
  ctx.translate(canvas.width / 2, canvas.height / 2);
  ctx.rotate(rotate);
  ctx.fillText(text, xOffset, yOffset);
  ctx.restore();
};
